@import "../../assets/scss/base";

.common_calender {
    .calendar-wrapper {
        position: $absolute;
        z-index: 999;
        right: 0;
        box-shadow: 0 0 10px rgba($black-color,.1);
        .rdrDays {
            .rdrSelected, .rdrInRange, .rdrStartEdge, .rdrEndEdge {
                background: $primary-color;
            }
        }
    }
}