@import '../../assets/scss/base';

.notfound {
    max-width: 767px;
    margin: 0 $auto;
    line-height: 1.4;
    text-align: $center;
    min-height: calc(100vh - 180px);
    @include flexCenter;
    @include flex-direction (column);
    .notfound-404 {
        width: 100%;
        margin-bottom: 20px;
        position: $relative;
        z-index: -1;
        h1 {
            text-shadow: -6px -1px 0 $white-color, 1px 1px 0 $grey-color-200;
            font-weight: $font-extrabold;
            text-transform: $uppercase;
            @include font-size (16);
            color: $primary-color;
            letter-spacing: 1px;
        }
        h2 {
            font-size: 42px;
            color: $secondary-color;
            font-weight: $font-bold;
            text-transform: $uppercase;
            text-shadow: 0 2px 0 $white-color;
            letter-spacing: 13px;
            margin: -20px 0 0;
        }
    }
}