@import '../../assets/scss/base';

.inner_banner {
    position: $relative;
    isolation: isolate;
    padding: 20px 0;
    z-index: 1;
    min-height: 470px;
    max-height: 470px;
    .banner_content {
        margin-top: 100px;
        @media (min-width:1200px){
            margin-top: 130px;
        }
    }
}

.job_search_filter {
    position: $relative;
    border: 0;
    z-index: 11;
    margin-top: -120px;
    .searchbar {
        margin-left: 0!important;
    }
    .d-inline-block {
        display:$block!important
    }
    .form-control{
        background-color: $white-color!important;
    }
}

.rcp_select {
    position: $relative;
    .rdp {
        padding: 20px;
        background: $primary-color;
        color: $white-color;
        position: $absolute;
        top: calc(100% + 10px);
        right: 10px;
        z-index: 1;
        margin: 0;
        @include border-radius (15px);
        &-button {
            &:hover:not([disabled]):not(.rdp-day_selected) {
                background-color: $secondary-color;
            }
        }
    }
}

.job_listing {
    position: $relative;
    z-index: 1;
}