@import '../../../../assets/scss/base';

.filter_btn {
    margin-top: -20px;
}

.arrow{
    &_direction {
        &_right {
            .show_hide_pwd {
                width:90px; height: 100%;
                right: 0; top: 0;
                @include flexbox();
                @include align-items ($center);
                @include justify-content (flex-end);
                padding-right: 15px;
                img {
                    @include transform (rotate(-90deg));
                    @include transform-origin (9px $center);
                }
            }
        }
    }
}

.branch_selection_view{
    width: 100%; 
    background: $white-color;
    @include border-radius (7px);
    position: $relative;
    @include flexbox;
    padding-right: 100px;
    &.form-control {
        padding:0;
        padding-right: 100px;
    }
    .tag_list {
        @include flexbox;
        overflow-y: $hidden;
        overflow-x: $auto;
    }
    .branch_item {
        margin: 2.5px;
        min-height: 40px; 
        @include flexCenter();
        padding: 7px 10px;
        @include font-size (.90);
        @include border-radius (5px);
        font-weight: $font-medium;
        white-space: nowrap;
        color: rgba(16, 70, 81, 0.5);
        background-color: $grey-color-500;
        em {
            cursor: $pointer;
            margin-left:4px;
            img {
                width: 10px;
            }
        }
    }
}

.more_item {
    position: $absolute;
    right: 40px;
    top: 0;
    height:100%;
    font-size: .75rem;
    white-space: nowrap; 
    @include flexCenter();
}