@import '../../../../../assets/scss/base';

// Loader
.spinner {
    @include center(both);
}

.lds-roller {
    display: $inline-block;
    position: $relative;
    width: 45px;
    height: 45px;
    top: -40px;
    margin-left: -50px;
    div {
        animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        transform-origin: 40px 40px;
        &::after {
            content: " ";
            display: $block;
            position: $absolute;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background: $base-color;
            margin: -4px 0 0 -4px;
        }
        &:nth-child(1) {
            animation-delay: -0.036s;
            &::after {
                top: 63px;
                left: 63px;
            }
        }
        &:nth-child(2) {
            animation-delay: -0.072s;
            &:after {
                top: 68px;
                left: 56px;
            }
        }
        &:nth-child(3) {
          animation-delay: -0.108s;
          &::after {
            top: 71px;
            left: 48px;
          }
        }
        &:nth-child(4) {
            animation-delay: -0.144s;
            &::after {
                top: 72px;
                left: 40px;
            }
        }
        &:nth-child(5) {
            animation-delay: -0.18s;
            &::after {
                top: 71px;
                left: 32px;
            }
        }
        &:nth-child(6) {
            animation-delay: -0.216s;
            &:after {
                top: 68px;
                left: 24px;
            }
        }
        &:nth-child(7) {
            animation-delay: -0.252s;
            &::after {
                top: 63px;
                left: 17px;
            }
        }
        &:nth-child(8) {
            animation-delay: -0.288s;
            &::after {
                top: 56px;
                left: 12px;
            }
        }
    }
  }

  @keyframes lds-roller {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }


.no_chat {
    height: 100%;
    @media (max-height:650px){
        max-height: 280px;
    }
    figure {
        width: 550px;
        margin: 0 $auto;
        img {
            max-height: 300px;
        }
    }
    span {
        line-height: 1.4;
        display: $block;
    }
}

.message_box {
    width: calc(100% - 15px);
    position: $absolute;
    left: 0;
    bottom: 0;
    z-index: 1;
    height: 62px;
    margin: 0 15px;
    @include border-radius (14px);
    background-color: $white-color;
    border: 1px solid $grey-color-700;
    .message_input {
        textarea {
            border: 0;
            resize: $none;
            min-height: 60px;
            color: $base-color;
            @include font-size(1);
            background: $white-color;
            box-shadow: $none!important;
            padding-right: 120px;
        }
        .msg_icons {
            .file_attachment {
                border-right: 1px solid $grey-color-700;
                display: $inline-block;
                text-align: $center;
                padding:5px 10px;
                cursor: $pointer;
                width: 56px;
                .attachment_dropdown {
                    max-width:280px;
                    position: $absolute;
                    bottom: calc(100% + 20px);
                    left: 0;
                    padding: 5px;
                    text-align: $center;
                    @include border-radius (8px);
                    background-color: $white-color;
                    border: 1px solid $grey-color-700;
                    z-index: 1;
                    .file_type {
                        position: $relative;
                        padding: 5px 10px;
                        text-align: $center;
                        input[type="file"] {
                            position: $absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            @include opacity (0);
                            z-index: 11;
                        }
                        em {
                            margin: 0 $auto 5px;
                            @include border-radius (7px);
                            background: linear-gradient(180deg, $primary-color 7.27%, #114752 100%);
                            width: 38px;
                            height: 38px;
                            @include flexCenter;
                            svg {
                                width: 18px;
                                path {
                                    fill: $white-color;
                                }
                            }
                        }
                        .h6 {color: rgba($base-color, .7); display: $block;}
                    }
                }
            }
        }
        .btn {
            min-width: 100px;
            position: $absolute;
            right: 7px;
            top: 7px;
            z-index: 1;
        }
    }
}

.media-modal {
    img {
        @include border-radius (15px);
    }
}
.Modal {
    .btn-modal {
        background: $primary-color !important;
        @include border-radius (50%);
        width: 40px; height: 40px;
        @include flexCenter();
        top: 5px;
        right: 5px;
        &:hover {

        }
        img {
            width: 16px;
        }
    }
    &.chat_modal {
        .modal-content {
            min-width: 300px;
            width: $auto;
            margin: 0 $auto;
            img {
                max-height: 540px;
                object-fit: $cover;
                object-position: $top $center;
                @media (min-height:750px){
                    max-height: 760px;
                }
                @media (min-height:650px){
                    max-height: 640px;
                }
            }
        }
    }
}