@import '../../assets/scss/base';

.progressBar {
  position: $absolute;
  bottom: 0;
  left: 0;
  height: 16px;
  left: 15px;
  width: calc(100% - 26px);
  @include border-radius(50px);
  span {
    font-size: 11px;
    font-weight: $font-bold;
  }
  .progresstext {
    background-color: $secondary-color;
    padding: 5px 10px;
    @include font-size(0.8);
    color: $white-color;
    top: -35px;
    right: 15px;
    text-align: $center;
    position: $relative;
    display: $inline-block;
    @include border-radius(6px);
    box-shadow: 0 0 10px rgba($base-color, 0.2);
    &::before {
      content: "";
      border-top: 10px solid $secondary-color;
      border-left: 10px solid $transparent;
      border-right: 10px solid $transparent;
      position: $absolute;
      bottom: -7px;
      left: calc(50% - 14px / 2);
    }
  }
}
