// _switch-toogle.scss
@import '../../assets/scss/base';

.toggle {
	cursor: $pointer;
	display: inline-block;
    margin-bottom: 0;
}
.toggle-switch {
	display: inline-block;
	background: $grey-color-100;
	border-radius: 16px;
    width: 48px;
    height: 25px;
	position: $relative;
	vertical-align: middle;
	transition: background 0.25s;
	&:before,
	&:after {
		content: "";
        position: $absolute;
        display: $block;
        border-radius: 50%;	
        transition: left 0.25s;
	}
	&:before {
	    background: $white-color;
		border-radius: 50%;
		width: 19px;
		height: 19px;		
		top: 3px;
		left: 4px;		
	}
    &:after {		
		background: $grey-color-100;
		width: 8px;
		height: 8px;
	    top: 9px;
		left: 9px;		
	}
	.toggle:hover &:before {
		background: $white-color;		
	}
	.toggle-checkbox:checked + & {
		background: $base-color;
		&:before {
			left: calc(100% - 22px);
		}
        &:after {
			left: calc(100% - 16px);
            opacity: 0;
		}
	}
}
.toggle-checkbox {
	position: $absolute;
	visibility: $hidden;
}
.toggle-label {
	margin-left: 10px;
	position: $relative;
	top: 2px;
	font-size: $f16 ;
	line-height: 1.3;
	@media (min-width:1440px) {
		@include font-size (1.1);
		line-height: 1.5;
	}
}