@import '../../assets/scss/base';

.react {
    &-select {
        min-width: 145px;
        .react {
            &-prefix {
                &__multi-value {
                    margin: 1px;
                    &__label {
                        @include font-size (.80);
                    }
                }
                &__indicator {
                    padding: 8px 8px 8px 0;
                    &-separator {
                        display: $none;
                    }
                    svg {
                        path {
                            fill: $base-color;
                        }
                    }
                }
                &__placeholder {
                    font-weight: $font-semibold;
                    color: $base-color;
                }
                &__single-value {
                    font-weight: $font-semibold;
                }
                &__control {
                    border-color: $grey-color-700;
                    @include border-radius (10px);
                    box-shadow: $none !important;
                    min-height: 50px;
                    @media (min-width:1300px) {
                        max-height: 50px;
                        min-height: 50px;
                    }
                    &:hover {
                        border-color: $grey-color-700;
                    }
                }
                &__input-container {
                    font-weight: $font-semibold;
                    color: $base-color;
                }
                &__value-container {
                    padding: 2px 0px 2px 15px;
                    @include flex-wrap (nowrap);
                    .more_item {
                        @include font-size(.75);
                        white-space: nowrap;
                        margin-left: 6px;
                    }
                }
                &__option {
                    border-top:1px solid $white-color;
                    border-bottom:1px solid $white-color;
                    @include font-size (1);
                    &--is-focused, &:active {
                        background-color: rgba($secondary-color,.8);
                        color: $white-color;
                    }
                    &--is-selected {
                        background-color: $primary-color;
                        .checkbox {
                            input {
                                &:checked {
                                    &+label {
                                        color: $white-color;
                                        &::before {
                                            border-color: $white-color;
                                        }
                                        &::after {
                                            border-color: $white-color;
                                        }
                                    }
                                }
                            }
                            label {
                                &::before {

                                }
                            }
                        }
                    }            
                }
            }
        }
    }
}
.green {
    .react-prefix {
        &__control {
            background-color: rgba($badge-success, .2);
            &:hover {
                border-color: $badge-success;
            }
        }
        &__single-value {
            color: $success-color;
        }
    }
}
.red {
    .react-prefix {
        &__control {
            background-color: rgba($error-color, .3);
            &:hover {
                border-color: $error-color;
            }
        }
        &__single-value {
            color: $error-color1;
        }
    }
}
.admin_action {
    margin: 15px 0 0;
    .react {
        &-select {
            margin-left: 10px;
            min-width: 130px;
            z-index: 11;
            .react {
                &-prefix {
                    &__placeholder {
                        color: $base-color;
                    }
                    &__indicator {
                        svg {
                            path {
                                fill: $base-color;
                            }
                        }
                    }
                    &__control {
                        height: 48px;
                        color: $base-color;
                        @include font-size (1);
                        background: $transparent;
                        border: 2px solid $base-color;
                    }
                }
            }
        }
    }
    .btn-sm {
        min-width: 120px;
        @include border-radius (10px);
    }
}