// _theme.scss

html {
    scroll-behavior: smooth;
    height: 100%;
}
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background: $white-color;
    color: $base-color;
    font-family: $default-font;
    font-weight: $font-regular;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    @include font-size(1);
    height: 100%;
    overflow-x: $hidden;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}
ul {
    margin: 0;
    padding: 0;
}
input {
    outline: 0 !important;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
    font-weight: $font-semibold;
    line-height: 1.2;
}
h1,
.h1 {
    @include font-size(3.2);
    @media (max-width:1200px) {
        @include font-size(2.6);
    }
    @media (max-width:767px) {
        @include font-size(2.3);
    }
}
h2,
.h2 {
    @include font-size(2.5);
    @media (max-width:1200px) {
        @include font-size(2.3);
    }
}
h3,
.h3 {
    @include font-size(2.3);
    @media (max-width:1200px) {
        @include font-size(2);
    }
}
h4,
.h4 {
    @include font-size(2);
}
h5,
.h5 {
    @include font-size(1.5);
}
h6,
.h6 {
    @include font-size(1.1);
    font-weight: $font-semibold;
}

p {
    color: $base-color;
    &:last-child {
        margin-bottom: 0
    }
}
img {
    border: 0;
    height: $auto;
    outline: 0 !important;
    @include no-select;
}
a {
    cursor: $pointer;
    color: $primary-color;
    text-decoration: $none;
    @include transition (all, .5s, ease-in-out);
    &:hover {
        text-decoration: $none;
        color: $base-color;
    }
}

/* Scroll */
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}
/* Track */
::-webkit-scrollbar-track {
    background: rgba($primary-color, .4);
}
/* Handle */
::-webkit-scrollbar-thumb {
    background: $primary-color;
    @include border-radius (10px);
}

.container {
    @media (min-width:1440px) {
        max-width: 1400px;
    }
    @media (min-width:767px) and (max-width:1200px) {
        max-width: 760px;
    }
}

input {
    // &::-webkit-calendar-picker-indicator {
    //     appearance: $none;
    //     display: $none;
    // }
    &::-webkit-inner-spin-button,
    &::-webkit-calendar-picker-indicator {
        // visibility: hidden !important;
        @include opacity (0);
        cursor: $pointer;
        // // visibility: $hidden;
        // position: $absolute;
        // width: 100%;
        // top: 0;
        // height: 100%;
        // left: 0;
    }
    &[type=time],
    &[type=datetime-local] {
        background: url(../icons/clock_icon.svg) no-repeat;
        background-position: right 23px $center;
    }
    &[type=date] {
        background-image: url(../icons/calendar_icon.svg);
        background-repeat: $no-repeat;
        background-position: right 23px $center;
        background-size: 18px;
    }
    &[type=time] {
        background-image: url(../icons/down_arrow.svg);
        background-repeat: $no-repeat;
        background-position: right 23px $center;
        background-size: 16px;
    }
    &::-webkit-inner-spin-button {
        -webkit-appearance: $none;
        display: $none;
    }
    // &:-webkit-autofill,
    // &:-webkit-autofill:hover,
    // &:-webkit-autofill:focus,
    // &:-webkit-autofill:active {
    //     -webkit-text-fill-color: $base-color;
    //     -webkit-background-clip: text !important;
    //     -webkit-box-shadow: inset 5px 5px 4px $grey-color-400, inset -5px -5px 4px $grey-color-400 !important;
    // }
}

label {
    line-height: 1.1;
    color: $base-color;
    margin-bottom: 7px;
    @include font-size (1);
    font-weight: $font-medium;
}

// Button CSS
.btn {
    outline: 0 !important;
    cursor: $pointer;
    line-height: 1;
    position: $relative;
    white-space: nowrap;
    text-transform: $capitalize;
    @include transition (all, .5s, ease-in-out);
    @include border-radius (5px);
    min-height: 38px;
    &[disabled],
    &.disabled {
        @include opacity (0.6);
        color: $white-color;
        pointer-events: $none;
    }
    &:focus,
    &:active {
        box-shadow: $none !important;
    }
    em {
        margin-right: 10px;
        img {
            width: 18px;
        }
    }
    &-lg {
        @include border-radius (10px);
        font-weight: $font-semibold;
        @include font-size(1.1);
        min-height: 58px;
        min-width: 180px;
    }
    &-md {
        font-weight: $font-semibold;
        @include font-size(1);
        padding: 1rem 1.5rem;
        // overflow: $hidden;
        min-height: 50px;
        min-width: 170px;
        @include border-radius (7px);
        @media (min-width:1441px) {
            min-height: 54px;
            @include border-radius (8px);
        }
    }
    &-sm {
        padding: .55rem 1rem;
        font-weight: $font-semibold;
        @include font-size(.90);
        overflow: $hidden;
        min-width: 90px;
        min-height: 48px;
        max-height: 48px;
    }
    &-xs {
        padding: 5px;
        text-align: $center;
        font-weight: $font-semibold;
        @include font-size(.90);
        overflow: $hidden;
        height: 46px;
        width: 46px;
        em {
            margin-right: 0;
        }
        img {
            width: 16px;
        }
    }
    &-primary {
        border: 0;
        background-color: $secondary-color;
        background: radial-gradient(95.71% 95.71% at 50.16% 4.29%, $secondary-color 0%, $dark-green-color 100%);
        color: $white-color;
        &:hover,
        &:active {
            background: radial-gradient(95.71% 95.71% at 10.16% 4.29%, $dark-green-color 0%, $secondary-color 100%);
            text-decoration: $none;
        }
    }
    &-secondary {
        background-color: $secondary-color;
        background: radial-gradient(95.71% 95.71% at 50.16% 4.29%, #20606D 0%, #124853 100%);
        color: $white-color;
        &:hover {
            background: radial-gradient(95.71% 95.71% at 50.16% 4.29%, #124853 0%, #20606D 100%);
            color: $white-color;
        }
    }
    &-secondaryGrey {
        background-color: rgb(192 210 213);
        background: radial-gradient(95.71% 95.71% at 50.16% 4.29%, rgb(192 210 213) 0%, rgb(192 210 213) 100%);
        color: $white-color;
        &:hover {
            background: radial-gradient(95.71% 95.71% at 50.16% 4.29%,  rgb(143, 157, 159) 0%,  rgb(143, 157, 159) 100%);
            color: $white-color;
        }
    }
    &-outline-primary {
        color: $base-color;
        background: $transparent;
        @include border-radius (10px);
        border: 2px solid $primary-color;
        &:hover {
            background: $base-color;
            border-color: $primary-color;
        }
    }
    &-outline-secondary {
        color: $white-color;
        color: $base-color;
        background: $transparent;
        @include border-radius (6px);
        border: 2px solid $secondary-color;
        &:hover {
            color: $white-color;
            background: $base-color;
            border-color: $base-color;
            svg {
                path {
                    fill: $white-color;
                }
            }
        }
    }
    &-transparent {
        background-color: $transparent;
        border-color: $transparent;
        color: $white-color;
        border: 0;
        &:hover {
            color: $white-color;
            border-color: $transparent !important;
            background-color: $transparent !important;
        }
    }
    &-white {
        background: $white-color;
        box-shadow: 0px 1px 16px rgba(92, 92, 92, 0.23);
    }
    &-danger {
        background: $error-color;
    }
    &-grey {
        background: $grey-color-400;
        @include border-radius (10px);
        text-align: $center;
        padding: 8px 1rem;
        line-height: 1.3;
    }
    &-badge {
        cursor: $pointer;
        max-width: 40px;
        min-width: 40px;
        min-height: 40px;
        max-height: 40px;
        text-align: $center;
        @include flexCenter;
        @include border-radius(5px);
        img {
            width: 16px;
            max-height: 16px;
        }
    }
    &-default {
        background: $base-color;
        cursor: $pointer;
    }
    &-Verified {
        background: $secondary-color;
    }
    &-Unverified {
        background: $error-color;
    }
    &-Pending {
        background: $blue-color;
    }
    &-notUploaded {
        background: $dark-grey-color;
    }
}
.badge {
    &-grey {
        background: #e1dede !important;
        color: $base-color;
    }
    &-md {
        line-height: 1.5;
    }
    &-sm {
        line-height: 1.5;
        padding: 0.5rem 1rem;
    }
}

.white_control {
    border: 1px solid $grey-color-700;
    @include border-radius(5px);
    background: $white-color;
    padding: 0.53rem 1.8rem 0.56rem 0.75rem;
    min-width: 80px;
    text-align: $left;
    position: $relative;
    em {
        position: $absolute;
        right: 10px;
        top: 50%;
        @include transform(translateY(-50%));
        img {
            width: 18px;
        }
    }
    label {
        margin: 0;
    }
}

.form {
    &-control {
        border: 1px solid $grey-color-700;
        @include border-radius (10px);
        font-weight: $font-regular;
        background-color: $grey-color-500;
        @include font-size (1);
        color: $base-color;
        min-height: 50px;
        max-height: 50px;
        padding: .88rem 1.2rem;
        @include transition (all, .5s, ease-in-out);
        @media (min-width:1300px) {
            min-height: 50px;
            @include font-size (1.1);
        }
        &:focus {
            background-color: $white-color;
            box-shadow: 0 0 10px rgba($primary-color, .2);
            border-color: $primary-color;
        }
        &[readonly] {
            background: $grey-color-300;
        }
        &.disabled,
        &:disabled {
            @include opacity (.5);
        }
        &-sm {
            font-size: $f12+2;
            min-height: 46px;
            padding: 14px 15px 12px;
            border-radius: 5px !important;
        }
        span {
            line-height: 1;
        }
    }
}
.field_white {
    .form-control {
        background-color: $white-color;
    }
}
select {
    background-image: url(../icons/down_arrow.svg);
    background-repeat: $no-repeat;
    background-size: 16px;
    background-position: $center right 15px;
    appearance: $none;
    -webkit-appearance: $none;
    padding-right: 24px;
    text-overflow: ellipsis;
    outline: $none !important;
    text-transform: $capitalize;
    option {
        background: $base-color;
        color: $white-color;
    }
}
textarea {
    &.form-control {
        min-height: 140px;
    }
}

.input-group {
    .form-control {
        @include border-radius (10px!important);
        padding-left: 55px;
    }
    &-text {
        @include border-radius (10px);
        @include justify-content ($center);
        background-color: $grey-color-500;
        height: calc(100% - 2px);
        @include font-size (1);
        text-align: $center;
        min-width: 40px;
        border-right: 0;
        position: $absolute;
        left: 0; top: 1px;
        border:$none;
        z-index: 11;
        @media (min-width:1600px){
            @include font-size (1.1);
        }
        &:after {
            content: '';
            width: 1px;
            height: calc(100% - 16px);
            background-color: $grey-color-700;
            position: $absolute;
            top: 8px;
            right: 0;
            z-index: 1;
        }
    }
    &:focus {
        .input-group-text {
            box-shadow: 0 0 10px rgba($primary-color, .2);
            background-color: $white-color;
            border-color: $primary-color;
        }
    }
}

// Placeholder CSS
::placeholder {
    color: $base-color;
    opacity: 1;
}
:-ms-input-placeholder {
    color: $base-color;
}
::-ms-input-placeholder {
    color: $base-color;
}

.error {
    line-height: 1;
    margin-top: 4px;
    display: $block;
    color: $error-color;
    @include font-size (.85);
    font-weight: $font-regular;
}

// Input File Default
[type="file"] {
    color: $base-color;
    @include font-size (.86);
    &::-webkit-file-upload-button {
        background: radial-gradient(95.71% 95.71% at 50.16% 4.29%, $secondary-color 0%, $dark-green-color 100%);
        @include border-radius (7px);
        text-transform: $capitalize;
        @include font-size (.85);
        color: $white-color;
        padding: 9px 20px;
        cursor: $pointer;
        outline: $none;
        border: 0;
        top: 2px;
        position: $relative;
        @include transition (all, 1s, ease);
        @media (max-width:1200px) {
            padding: 7px 18px;
            top: 0px;
        }
    }
}
.white_control {
    border: 1px solid $grey-color-700;
    @include border-radius(5px);
    background: $white-color;
    padding: 0.53rem 1.8rem 0.56rem 0.75rem;
    min-width: 80px;
    text-align: $left;
    position: $relative;
    em {
        position: $absolute;
        right: 10px;
        top: 50%;
        @include transform(translateY(-50%));
        img {
            width: 18px;
        }
    }
    label {
        margin: 0;
    }
}

// Input file Custom
.input_file input:disabled~label {
    @include opacity (.2);
}
.input_file {
    position: $relative;
    min-width: 260px;
    .position-relative {
        position: $static !important;
        &.mb-4 {
            margin-bottom: 0 !important;
        }
        .form-control {
            position: $absolute;
            bottom: 0;
            left: 0;
            z-index: 11;
            @include opacity (0);
        }
    }
    label {
        &.data_label {
            min-width: 120px;
            color: rgba($base-color, .3);
            position: $relative;
            &:disabled {
                @include opacity (.1);
            }
            &::after {
                content: attr(data-label);
                position: $absolute;
                right: 10px;
                top: 5px;
                background: radial-gradient(95.71% 95.71% at 50.16% 4.29%, $secondary-color 0%, $dark-green-color 100%);
                @include border-radius (7px);
                text-transform: $capitalize;
                @include font-size (.85);
                color: $white-color;
                padding: 9px 20px;
                cursor: $pointer;
                outline: $none;
                @include transition (all, 1s, ease);
                z-index: 1;
                @media (max-width:1300px) {
                    top: 5.5px;
                    right: 5px;
                }
            }
        }
    }
}

// Checkbox
.checkbox {
    position: $relative;
    display: $inline-block;
    input[type=checkbox] {
        padding: 0;
        height: 100%;
        width: 100%;
        @include opacity (0);
        position: $absolute;
        cursor: $pointer;
        z-index: 11;
        &:checked+label:after {
            content: '';
            display: $block;
            position: $absolute;
            top: 3px;
            left: 9px;
            width: 6px;
            height: 14px;
            border: solid $primary-color;
            border-width: 0 2px 2px 0;
            @include transform (rotate(45deg));
        }

    }
    label {
        margin-bottom: 0;
        cursor: $pointer;
        line-height: 1.1;
        position: $relative;
        font-weight: $font-regular;
        color: rgba($base-color, .8);
        &::before {
            content: '';
            -webkit-appearance: $none;
            appearance: $none;
            background-color: $transparent;
            border: 2px solid $primary-color;
            @include border-radius (5px);
            display: $inline-block;
            position: $relative;
            vertical-align: middle;
            margin-right: 9px;
            padding: 10px;
            cursor: $pointer;
        }
    }
}

// radio tyope css
.radio-buttons {
    position: $relative;
    input[type="radio"] {
        opacity: 0;
        z-index: 1;
        padding: 0;
        height: 100%;
        width: 100%;
        @include opacity (0);
        position: $absolute;
        cursor: $pointer;
        &:checked {
            &+ label {
                &::before {
                    border-color:$primary-color;
                }
                &::after {
                    transform: translateY(-50%) scale(0.95);
                }
            }
        }
  }
  label {
    cursor: $pointer;
    position: $relative;
    font-size: $f16;
    line-height: 22px;
    font-weight: $font-medium;
    padding-left: 36px;
    color: $base-color;
    margin-bottom: 0;
    display: flex;
    align-items: center;
    &::before {
        content: "";
        position: $absolute;
        width: 24px;
        height: 24px;
        background-color: $white-color;
        border: 2px solid $base-color;
        border-radius: 50%;
        top: 50%;
        left: 0rem;
        @include transform(translateY(-50%));
        @include transition(border-color, 400ms , ease);

    }
    &::after {
        content: "";
        position: absolute;
        width: 14px;
        height: 14px;
        background-color: $primary-color;
        border: 1px solid $primary-color;
        border-radius: 50%;
        top: 50%;
        left: 5px;
        transform: translateY(-50%) scale(0);
        transition: transform 400ms ease;
    }
  }
}
// ends css
.text-primary {
    color: $primary-color !important;
}

.text-mute {
    color: rgba($base-color, .6);
}

.pointer {
    cursor: $pointer;
}

.form_white {
    .form-control {
        background-color: $white-color;
    }
}

// Badge
.badge {
    display: $inline-block;
    &-md {
        min-width: 110px;
        text-align: $center;
        padding: .60rem 1.5rem;
        @include font-size (1);
        font-weight: $font-regular;
        @include border-radius (7px);
    }
    &-success {
        background-color: rgba($badge-success, .3);
        color: $badge-success;
        &:hover {
            background-color: rgba($badge-success, .3);
        }
    }
    &-danger {
        background-color: rgba($error-color, .3);
        color: $error-color;
        &:hover {
            background-color: rgba($error-color, .3);
        }
    }
    &-info {
        background-color: rgba($blue-color, .3);
        color: $blue-color;
        &:hover {
            background-color: rgba($blue-color, .3);
            color: $blue-color;
        }
    }
}
// Background Color
.bg-grey {
    background: $grey-color-200;
}
.grey-bg {
    background: $grey-color-500;
}

.text-small {
    font-size: 14px;
}
.height-120 {
    min-height: 120px !important;
}

// RC Npm Time Picker
.rc-time-picker {
    width: 100%;
    pointer-events: $none;
    .rc-time-picker-input {
        @extend .form-control;
        text-transform: $uppercase;
    }
    &-panel-select {
        ul {
            li {
                text-transform: $uppercase;
            }
        }
    }
    &-input {
        text-transform: $uppercase;
    }
    .rc-time-picker-icon {
        cursor: $pointer;
        pointer-events: $auto;
        background: url(../icons/clock_icon.svg) $no-repeat;
        background-position: $right 15px $center;
        background-size: 20px;
        display: $inline-block;
        position: $absolute;
        right: 0; top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
}