@import '../../assets/scss/base';

::root {
    --bs-modal-bg: $white-color;
    --bs-modal-bg: $grey-color-700;
}

.ReactModal {
    overflow-x: $hidden;
    &__Overlay {
        backdrop-filter: blur(6px);
        background: rgba($black-color, 0.7)!important;
        width: 100%;
        height: 100%;
        position: $fixed;
        top: 0;
        left: 0;
        z-index: 1111;
    }
    &__Content {
        padding: 0 15px;
        overflow-x: $hidden;
        overflow-y: $auto;
        position: $fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        outline: 0;
        z-index: 1050;
    }
    &__Body--open {
        overflow: $hidden;
    }
}

.Modal {
    .modal {
        &-md {
            max-width: 530px;
        }
        &-xs {
            max-width: 450px;
            .modal-content {
                @include border-radius (1rem);
            }
        }
        &-dialog {
            margin: 1.75rem $auto;
            @media (min-width: 576px) {
                &-scrollable {
                    height: calc(100% - 3.5rem);
                    overflow-x: $hidden;
                    .modal-body {
                        overflow-x: $hidden;
                    }
                }
            }
            &-centered {
                min-height: calc(100% - 3.5rem);
            }
        }
        &-content {
            background: $white-color;
            @include border-radius (1.5rem);
            @media (max-width:820px) {
                @include border-radius (1rem);
            }
        }
        &-header {
            padding: 1rem 2rem;
            border: 0;
        }
        &-body {
            padding: 2.5rem 2rem;
            @media (max-width:820px) {
                padding: 2rem;
            }
        }
        &-footer {
            padding: 1rem 2rem;
        }
    }
    .btn-close {
        box-shadow: $none !important;
        background: var(--bs-modal-bg);
        @include opacity (1);
        cursor: $pointer;
        position: $absolute;
        right: 15px;
        top: 10px;
        padding: 0;
        width: 20px;
        height: $auto;
        z-index: 111;
        @media (max-width:820px) {
            right: 10px;
            top: 7px;
        }
        img {
            width: 100%;
        }
    }
}

.confirmation_modal {
    @media (min-width:1200px) {
        padding: 0 10px;
    }
    .modal_icon {
        width: 70px;
        height: 70px;
        padding: 10px;
        margin: 0 $auto;
        @include flexCenter;
        text-align: $center;
        @include border-radius (50%);
        background-color: $grey-color-300;
        img {
            width: 30px;
            height: $auto;
        }
        &.success_icon {
            background: #F3FAEC;
        }
        &.danger_icon {
            background: rgba($error-color, .4);
        }
    }
}