@import '.././../assets/scss/base';

.job_detail {
  .card-body {
    p {
      @include font-size (1.1);
      color: rgba($base-color, .8);
    }
  }
}

.job_location {
  font-weight: $font-regular;
  color: rgba($base-color, .9);
  font-weight: $font-regular!important;
  em {
    img {
      width: 16px;
    }
  }
}

.DetailRow {
  &_card {
    &_block {
      .card-body {
        &.job_body {
          .card {
            &.card_light_green {
              background: $grey-color-500;
            }
            &.card_white {
              background: $white-color;
            }
            @at-root {
              .card-title {
                @include border-radius(10px 10px 0 0);
                background: $base-color;
                text-align: $center;
                color: $white-color;
                margin-bottom: 0;
                padding: 1rem;
              }
            }
            .card-body {
              @include border-radius (0 0 10px 10px);
              text-align: $center;
              padding: 1rem;
              // background-color: $transparent;
              border: $none;
              outline: $none;
            }
          }
        }
        .ratingBlock {
          &:not(:last-child) {
            margin-bottom: 1rem;
          }
          &_title {
            font-weight: $font-regular;
            margin-bottom: 0;
          }
        }
      }
      // job timing collapse block
      .card_light_green {
        background: $grey-color-500;
      }
      .card_white {
        background: $white-color;
      }
      .job_timing {
        h5 {
          margin-bottom: 0;
          span {
            font-weight: $font-regular;
            font-size: calc($f16 - 2px);
            margin-left: 5px;
            line-height: 2;
          }
        }
      }
      .common_collapse_card {
        .card {
          &-header {
            padding: 1.125rem;
            @media (min-width: 1440px) {
              padding: 1.125rem 1.625rem;
            }
          }
        }
      }
    }
  }
}

.google_map {
  border: 2px solid $base-color;
  @include border-radius (15px);
  overflow: $hidden;
}

.job_body {
  .table {
    width: 100%;
    margin-bottom: 0;
    border-spacing: 0;
    th {
      padding: 6px 0;
      background: $transparent;
      color: rgba($base-color, 1);
      font-weight: $font-semibold;
      @include font-size(0.9);
    }
    td {
      @include font-size(0.9);
      padding: 6px 0;
      color: $base-color;
      font-size: calc($f16 - 2px);
      line-height: 1.3;
      border: $none;
      font-weight: $font-regular;
      text-align: $left;
    }
  }
}

// Rating
.ratingBlock {
  label {
      margin: 0;
      @include font-size (1);
      color: var(--base-color);
  }
  .ratingBlock_value {
    line-height: 1;
    @include flexbox();
    @include align-items ($center);
    em {
      img {
          width: 18px;
          display: $inline-block;
      }
    }
  }
}

.job_body {
  .table {
    width: 100%;
    margin-bottom: 0;
    border-spacing: 0;
    th {
      padding: 6px 0;
      background: $transparent;
      color: rgba($base-color, 1);
      font-weight: $font-semibold;
      @include font-size(0.9);
    }
    td {
      @include font-size(0.9);
      padding: 6px 0;
      color: $base-color;
      font-size: calc($f16 - 2px);
      line-height: 1.3;
      border: $none;
      font-weight: $font-regular;
      text-align: $left;
    }
  }
}

.shift_day {
  min-width: 130px;
}