@import '../../../../assets/scss/base';

$searchIcon: url(../../../../assets/icons/search_icon.svg);
$checkIcon: url(../../../../assets/icons/check_icon.svg);


.search_result {
    height: calc(100% - 20px);
}
.message_date {
    display: $block;
    text-align: $center;
    position: $relative;
    span {
        padding: 2px 10px;
        color: $primary-color;
        @include font-size (1);
        display: $inline-block;
        font-weight: $font-medium;
        text-transform: $capitalize;
        background: $grey-color-500;
    }
    &::before {
        content: '';
        position: $absolute;
        left: 0; top: calc(50%);
        width: 100%; height: 1px;
        background: $grey-color-300;
        z-index: -1;
    }
}
.chat {
    &_panel {
        height: calc(100dvh - 100px);
        margin-top: -25px;
        .chat {
            &_title {
                padding: 8px 0 15px;
            }
        }
        .dropDown {
            min-width: 180px;
            .dropDown-list {
                &:hover, &.active {
                    background-image: $checkIcon;
                    background-repeat: $no-repeat;
                    background-position: $right 12px $center;
                    background-size: 14px;
                    span {
                        color: $primary-color;
                    }
                }
                svg {
                    width: 26px;
                    height: 18px;
                    margin-right: 7px;
                }
            }
        }
    }
    &_leftBar {
        @media (min-width:991px){
            border-right:1px solid $grey-color-700;
            padding-right: 20px;
            width:350px;
        }
        @media (max-width:991px){
            width:100%;
        }
        .search_close {
            position: $absolute;
            right: 13px;
            top: 8px;
            z-index: 1;
            cursor: $pointer;
            img {
                width: 12px;
                height: $auto;
            }
        }
        .form-control {
            margin: 20px 0;
            background-image: $searchIcon;
            background-repeat: $no-repeat;
            background-position: 12px $center;
            background-size: 16px;
            padding-left: 36px;
            height: 45px;
            min-height: 40px;
            margin-bottom: 10px;
            line-height: 1;
            @include font-size (1);
            color: rgba($base-color, .7);
        }
        @at-root {
            .chat {
                &_list {
                    height: calc(100% - 100px);
                    [data-test-id="virtuoso-scroller"] {
                        margin-right: -23px;
                        [data-viewport-type="element"] {
                            padding-right: 20px;
                        }
                    }
                    li {
                        border: 1px solid $grey-color-700;
                        @include border-radius (6px);
                        background: $white-color;
                        margin: 0 0 15px;
                        cursor: $pointer;
                        padding: 10px;
                        &.active {
                            background-color: $dark-color-600;
                            border: 1px solid $primary-color;
                        }
                    }
                }
            }
            .chat_summary {
                margin: $auto 0;
                position: $relative;
                width: calc(100% - 50px);
                padding: 4px 90px 4px 12px;
                .h6 {
                    text-transform: $capitalize;
                    @include font-size (1);
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: $hidden;
                    line-height: 1.6;
                }
                p {
                    color: rgba($base-color, .8);
                    @include font-size (.90);
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: $hidden;
                }
                @at-root {
                    .msg_time {
                        position: $absolute;
                        top: 2px;
                        right: 0;
                        text-align: $right;
                        @include font-size (.90);
                        color: rgba($base-color, .8);
                    }
                    .unread_msg {
                        position: $absolute;
                        bottom: -3px;
                        right: 0;
                        padding: 5px;
                        color: $black-color;
                        @include flexCenter();
                        width: 26px; height: 26px;
                        @include border-radius (20px);
                        background-color: $grey-color-100;
                        border: 1px solid $grey-color-100;
                    }
                }
            }
        }
    }
    &_header {
        border-bottom:1px solid $grey-color-700;
        padding: 15px 5px;
    }
    &_rightBar {
        position: $relative;
        @media (min-width:991px){
            width: calc(100% - 330px);
        }
        @media (max-width:991px){
            display: $none;
            width: 100%;
        }
        .messages_list {
            height: calc(100dvh - 100px);
            .chat_main {
                height: calc(100% - 80px);
                .body_msg {
                    padding: 20px 0;
                    height:calc(100% - 20px);
                    position: $relative;
                    #chatList {
                        height: calc(100% - 34px);
                    }
                }
            }
        }
    }
    @at-root {
        .down_arrow {
            @include border-radius (50%);
            @include flexCenter;
            padding: 10px;
            width: 40px;
            height: 40px;
            svg {
                width: 30px;
            }
        }
    }
}
.btn_grey {
    background: rgba($base-color, .4);
    @include border-radius (4px);
    border: 0;
    width: 34px;
    min-height: 34px;
    @include flexCenter;
    svg {
        width: 16px;
    }
}
.chat_messages {
    // margin-right: -20px;
    li {
        padding-right: 15px;
        .msg_group {
            padding: 20px 0 0 20px;
            &.list_gap{
                padding-top: 0;
            }
            @at-root {
                .message_block {
                    max-width: 80%;
                    .msg_time_name {
                        position: $relative;
                        .h6 {
                            @include font-size (1);
                            color: $base-color;
                        }
                        .text-small {
                            line-height: 1;
                            position: $relative;
                            @include font-size (.90);
                            color: rgba($base-color, .6);
                            &::after {
                                content: '';
                                background: rgba($base-color, .5);
                                @include border-radius (50%);
                                height: 6px; width: 6px;
                                position: $absolute;
                                top: calc(50% - 6px/2);
                            }
                        }
                    }
                    .text_msg {
                        background: $white-color;
                        word-break: break-word;
                        padding: 10px 15px;
                        margin: 5px 0 0;
                        width: $auto;
                        @include font-size (.90);
                    }
                }
            }
        }
        &.outgoing_msg {
            text-align: $right;
            .msg_group {
                @include flex-direction (row-reverse);
                .avtar {
                    margin-left: 15px;
                }
                .text-small {
                    margin-right:30px;
                    &::after {
                        // display: $none;
                        right: -18px;
                    }
                }
                .message_block {
                    margin-left: $auto;
                    @include flex-wrap (wrap);
                    @include justify-content (flex-end);
                    @include align-items (flex-end);
                    .msg_time_name {
                        display: $none;
                        @include justify-content (end);
                        @include flex-direction (row-reverse);
                    }
                    .text_msg {
                        @include border-radius (9px 0px 9px 9px);
                    }
                }
            }
        }
        &.incoming_msg {
            text-align: $left;
            .msg_group {
                .avtar {
                    margin-right: 15px;
                }
                .text-small {
                    margin-left:30px;
                    &::after {
                        content: '';
                        left: -18px;
                    }
                }
                .message_block {
                    margin-right: $auto;
                    @include justify-content (flex-start);
                    @include align-items (flex-start);
                    .msg_time_name {
                        @include justify-content (flex-start);
                    }
                }
                .text_msg {
                    @include border-radius (0 9px 9px 9px);
                }
            }
        }
    }
}

.media_attachment {
    position: $relative;
    max-width: 250px;
    margin-top: 20px;
    @include border-radius (10px);
    cursor: $pointer;
    video, img {
        width: 100%;
        min-width: 100px;
        max-height: 250px;
        object-fit: $cover;
        @include border-radius (10px);
    }
    .download_btn {
        position: $absolute;
        right: 5px;
        bottom: 5px;
    }
}
.download_btn {
    @include transition (all, .5s, ease-in-out);
    box-shadow: 0 0 10px rgba($secondary-color, .4);
    cursor: $pointer;
    @include border-radius(50%);
    background: rgba($secondary-color, .7);
    @include flexCenter();
    text-align: $center;
    width: 34px;
    height: 34px;
    border: 0;
    &:hover {
        background:$secondary-color;
    }
    svg {
        width: 22px;
        height: 22px;
    }
}

.doc_attachment {
    em {
        svg {
            width: 30px;
            height: 30px;
        }
    }
}

.back_chat {
    padding: 0;
    border: 0;
    @media (min-width:991px) {
        display: $none;
    }
    svg {
        width: 30px;
    }
}

.no_users{
    height: 100%;
    text-align: $center;
}
