@import '../../assets/scss/base';

.range_slider {
    min-height: 44px;
    .rc-slider {
        .rc-slider-track {
            background-color: $secondary-color;
        }
        .rc-slider-handle {
            @include opacity (1);
            background: $dark-green-color;
            width: 16px; height: 16px;
            border-color: $white-color;
        }
    }
    span {
        color: $white-color;
    }
}

.admin_panel {
    .range_slider {
        span {
            color:$base-color;
        } 
    }
  }

