@import '../../assets/scss/base';

.job_btn {
	.btn {
		&:first-child {
			margin-left: 0 !important;
		}

		&:focus,
		&:active {
			border-color: $transparent !important;
		}

		&.selected {
			background-color: $primary-color;
			color: $white-color;
		}
	}
}

.label_title {
	display: $block;
	position: $relative;
	z-index: 1;

	span {
		font-size: $inherit;
		padding-right: 10px;
		background: $white-color;
	}

	&::after {
		content: '';
		background-color: $grey-color-700;
		height: 1px;
		width: 100%;
		position: $absolute;
		left: 0;
		top: calc(50% + 2px);
		z-index: -1;
	}
}

.job_btn {
	.btn {
		transition: $none;

		&::after {
			content: '';
			background: $primary-color;
			width: 20px;
			height: 20px;
			position: $absolute;
			left: calc(50% - 10px);
			bottom: -10px;
			@include transform (rotate(135deg));
			@include opacity(0);
			z-index: 1;
		}

		&.selected {
			&::after {
				@include opacity(1);
			}

			.checkbox {
				label {
					color: $white-color;

					&::before {
						background-color: $white-color;
					}
				}
			}
		}
	}
}

.admin_view {
	.label_title {
		span {
			background: $grey-color-500;
		}
	}
}

.common_collapse_card {
	&.card {
		border-color: $grey-color-700;
		.card-body {
			background: $white-color;
			.accordion {
				&-item {
					background: $grey-color-500;
					@include border-radius(10px);
					margin-bottom: 1rem;
					overflow: $hidden;
					.accordion-header {
						.accordion-button {
							background: $grey-color-500;
							@media screen {
								padding: 1.125rem 1.5rem;
							}
							&:focus-visible, &:focus {
								border-color: transparent;
								box-shadow: none;
							}
							&::after{
								position: $absolute;
								right: 15px;
							}
							&:not(.collapsed) {
								&::after{
									transform: rotate(0deg) !important;
									background: url("../../assets/icons/bottom_arrow_icon.svg") $no-repeat;
								}
							}
							&.collapsed {
								&::after {
									transform: rotate(-90deg);
									background: url("../../assets/icons/bottom_arrow_icon.svg") $no-repeat;
								}
							}
						}
						.toggle-button {
							flex: 0 0 200px;
							width: 200px;
						}
						.week_btn {
							flex: 0 0 calc(100% - 180px - 50px);
							padding-right: 25px;
						}
						.timing,.status {
						span {
							font-weight: $font-regular;
							font-size: $f16;
							line-height: 1.3;
							color: rgba($base-color,0.5);
							@media screen and (min-width:1440px){
								@include font-size (1.1);
								line-height: 1.5;
							}
						}

					}
					.week_btn {
						.btn {

						}
					}
					}
					&:last-child {
						margin-bottom: 0;
					}
					.accordion_content {
						.rc-time-picker {
							&-input {
								background-color: $white-color;
							}
						}
					}
				}
				&-button {
					@include justify-content (space-between);
				}
			}
		}
	}
}