@import '../../../../../../assets/scss/base';

.service_listing{
    padding:20px;
    min-height: 60px;
    background-color: $white-color;
    @include border-radius (12px);
    border-color:$grey-color-700;
    .service_item {
        margin: 5px;
        min-height: 40px;
        line-height: 1.4;
        padding: 7px 16px;
        @include font-size (1);
        @include border-radius (10px);
        font-weight: $font-medium;
        color: rgba($base-color, 0.5);
        background-color: $grey-color-500;
    }
    em {
        cursor: $pointer;
        img {
            margin-top: -1rem;
            width: 8px;
        }
    }
}

.descriptionArea {
    input {
        min-height: 100px;
        @media screen and (min-width:1440px) {
            min-height: 135px;
        }
    }
}

.serviceRow {
    .btns {
        .btn {
            width: $auto;
            @media (max-width:1366px) {
                min-width: $initial;
            }

        }
    }
}