@import '../../assets/scss/base';

.hero_banner {
    position: $relative;
    isolation: isolate;
    padding: 20px 0;
    z-index: 1;
    min-height: 640px;
    @media (min-width:1600px) {
        min-height: 800px;
    }
    @media (max-width:1200px) {
        min-height: 540px;
    }
    @at-root {
        .hero_bg {
            pointer-events: $none;
            position: $absolute;
            left: 0;
            right: 0;
            top: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            img {
                height: 100%;
                object-fit: $cover;
                object-position: $bottom;
            }
        }
        .banner_info {
            min-height: 480px;
            @media (min-width:1600px) {
                min-height: 660px;
            }
            @media (max-width:1200px) {
                min-height: 400px;
            }
            figure {
                width: 100%;
                img {
                    max-height: 420px;
                    object-fit: contain;
                    @media (min-height:1200px){
                        max-height: 460px;
                    }
                }
            }
            .banner_content {
                color: $white-color;
                max-width: 80%;
                @media (min-width:1200px){
                    max-height: 500px;
                }
                h1, h2, h3, h4, h5, h6 {
                    text-overflow: ellipsis;
                    overflow: $hidden;
                    display: -webkit-box;
                    -webkit-line-clamp: 4;
                    -webkit-box-orient: vertical;
                    overflow: $hidden;
                }
                p {
                    color: rgba($white-color, .7);
                }
            }
        }
    }
}