@import "../../assets/scss/base";

.overlay{
    display: $none;
    position: $fixed;
    top: 0; left: 0;             
    background:rgba($black-color, .2);
    height: 100%;
    width: 100%;
    z-index: 1111;
    &.active {
        display: $block;
    }
}
.slideView {
    position: $fixed;
    top: 0;
    padding: 0;
    width: 450px;
    height: 100%;
    z-index: 999999;
    background: $white-color;
    @include border-radius (0);
    @include transition (all, .50s, ease-in-out);
    box-shadow: 0 20px 20px rgba($black-color, .3);
    @media (min-width:767.11px) {
        right: -460px;
    }
    @media (max-width:767px) {
        right: calc(-90% + 10px);
        width: 90%;
    }
    &.active {
        right: 0;
        &::before {
            display: $block;
        }
    }
    .slideView_body {
        background: $white-color;
        @include border-radius (0);
        height: calc(100% - 130px);
    }
    &_head {
        background: $white-color;
        .close-btn {
            width: 40px;
            height: 40px;
            padding: 5px;
            border: $none;
            @include flexCenter();
            @include border-radius (100vmax);
        }
    }
    .search_bar {
        .searchbar {
            margin-left: 0 !important;
            input {
                background-color: $grey-color-500;
            }
        }
    }
    &_body {
        .scrollBarSlide {
            height: calc(100% - 40px);
            overflow-x: $hidden;
            overflow-y: $auto;
            margin-top: 10px;
        }
        .radio-buttons, .checkbox {
            border-bottom: 1px solid $light-grey-color;
            padding: 0.75rem 0;
            display: $block;
            margin: 5px 0;
        }
    }
}

.bg-note {
    background: $thead-color;
    label {
        line-height: 1;
    }
}